import React from 'react';
import ArrowBackIos from '@mui/icons-material/ArrowBackIos';
import {
  Button,
  IconButton,
  Typography,
  ButtonGroup,
  Snackbar,
  Alert,
  Backdrop,
  CircularProgress
} from '@mui/material';
import { useParams } from 'react-router-dom';
import AdminIcon from '@mui/icons-material/AdminPanelSettingsOutlined';
import TextField from '@mui/material/TextField';
import SendIcon from '@mui/icons-material/SendRounded';

export function TasksView({ onGoBack, getAdmin, onSubmit, retry, defaultMode }) {
  const params = useParams();
  const [admin, setAdmin] = React.useState(getAdmin(params.id));
  if (!admin) {
    retry(params.id).then((r) => {
      if (r) {
        setAdmin(r);
      } else {
        onGoBack();
      }
    });
  }
  const [snack, setSnack] = React.useState({
    open: false,
    mode: 'info',
    time: 3000,
    message: ''
  });
  const [mode, setMode] = React.useState(defaultMode); // 0 || 4
  const [title, setTitle] = React.useState('');
  const [description, setDescription] = React.useState('');
  const SetTitle = (v) => setTitle(v.target.value);
  const SetDescription = (v) => setDescription(v.target.value);

  const doSubmit = () => {
    onSubmit({
      Title: title,
      Description: description,
      Status: mode,
      AssignTo: params.id
    }).then((r) => {
      if (r.status === 1) {
        setSnack({
          message: 'ارسال شد',
          mode: 'success',
          open: true,
          time: 5000
        });
      } else {
        setSnack({
          message: 'خطا در ارسال. کد ' + r.statusCode,
          mode: 'error',
          open: true,
          time: 5000
        });
      }

      setTimeout(() => {
        setSnack({ ...snack, open: false });
      }, snack.time);
    });
  };

  return !admin ? (
    <div className="row">
      <div className="col-12">
        <Backdrop open={true}>
          <CircularProgress />
        </Backdrop>
      </div>
    </div>
  ) : (
    <div className="row">
      <div className="col-12">
        <div className="row">
          <div className="col-12 text-start">
            {mode === 0 ? 'تعریف تسک' : 'ارسال پیام'}
            <IconButton onClick={onGoBack}>
              <ArrowBackIos />
            </IconButton>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <div className="row">
              <div className="col-12 text-center">
                <AdminIcon fontSize="large" />
              </div>
            </div>
            <div className="row">
              <div className="col-12 text-center p-2">
                <Typography variant="h5">{admin && admin.Username}</Typography>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12 p-1 px-3 text-center">
            <TextField
              label={'عنوان'}
              onChange={SetTitle}
              onBlur={SetTitle}
              value={title}
              variant="outlined"
              className="w-50"
            />
          </div>
        </div>
        <div className="row">
          <div className="col-12 p-1 px-3 text-center">
            <TextField
              label={'توضیحات'}
              variant="outlined"
              onChange={SetDescription}
              onBlur={SetDescription}
              value={description}
              multiline
              rows={4}
              maxRows={10}
              minRows={4}
              className="w-50 "
            />
          </div>
        </div>
        <div className="row">
          <div className="col-12 p-1 px-3 text-center">
            <ButtonGroup className="w-50">
              <Button
                className="w-50"
                variant={mode === 0 ? 'contained' : 'outlined'}
                onClick={() => setMode(0)}>
                تسک
              </Button>
              <Button
                className="w-50"
                variant={mode === 4 ? 'contained' : 'outlined'}
                onClick={() => setMode(4)}>
                پیام
              </Button>
            </ButtonGroup>
          </div>
        </div>
        <div className="row">
          <div className="col-6 mx-auto px-2 p-1 py-4">
            <Button
              variant="contained"
              disabled={!title || !description}
              fullWidth
              className="rounded-pill"
              onClick={doSubmit}
              color="success">
              ارسال
              <SendIcon sx={{ transform: 'rotate(-180deg)' }} fontSize="small" className="me-2" />
            </Button>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <Snackbar open={snack.open} anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}>
              <Alert severity={snack.mode} sx={{ width: '100%' }}>
                {snack.message}
              </Alert>
            </Snackbar>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TasksView;
